"use server"

import { auth } from "@/auth"
import { prisma } from "@/lib/prisma"
import { revalidatePath } from "next/cache"
import { z } from "zod"

const timeEntrySchema = z.object({
    workItemId: z.string().optional(),
    durationMinutes: z.coerce.number().min(1, "Duration must be at least 1 minute"),
    notes: z.string().optional(),
    date: z.string().refine((val) => !isNaN(Date.parse(val)), {
        message: "Invalid date",
    }),
})

export async function logTime(formData: FormData) {
    const session = await auth()
    if (!session?.user?.id) {
        return { error: "Unauthorized" }
    }

    const rawData = {
        workItemId: formData.get("workItemId") || undefined,
        durationMinutes: formData.get("durationMinutes"),
        notes: formData.get("notes"),
        date: formData.get("date"),
        projectId: formData.get("projectId") || undefined, // Allow direct project selection
    }

    const validated = timeEntrySchema.safeParse(rawData)

    if (!validated.success) {
        return { error: validated.error.flatten().fieldErrors }
    }

    const { workItemId, durationMinutes, notes, date } = validated.data
    const startedAt = new Date(date)

    // Duration in DB is seconds
    const durationSeconds = durationMinutes * 60

    try {
        let projectId: string | null = (rawData.projectId as string) || null
        let isBillable = true

        // Logic to determine Project and Billable status
        if (workItemId) {
            const task = await prisma.workItem.findUnique({
                where: { id: workItemId },
                select: { projectId: true, project: { select: { isBillable: true } } }
            })
            if (task) {
                projectId = task.projectId
                if (task.project) isBillable = task.project.isBillable
            }
        } else if (projectId) {
            const project = await prisma.project.findUnique({
                where: { id: projectId },
                select: { isBillable: true }
            })
            if (project) isBillable = project.isBillable
        }

        await prisma.timeEntry.create({
            data: {
                userId: session.user.id,
                workItemId: workItemId || null,
                projectId: projectId,
                durationSeconds,
                startedAt,
                notes,
                isBillable
            },
        })

        revalidatePath("/time")
        revalidatePath("/dashboard")
        revalidatePath("/work")
        return { success: true }
    } catch (error) {
        console.error("Failed to log time:", error)
        return { error: "Failed to log time" }
    }
}

export async function updateTimeEntry(id: string, formData: FormData) {
    const session = await auth()
    if (!session?.user?.id) return { error: "Unauthorized" }

    const durationMinutes = Number(formData.get("durationMinutes"))
    const notes = formData.get("notes") as string
    const date = formData.get("date") as string

    try {
        await prisma.timeEntry.update({
            where: { id },
            data: {
                durationSeconds: durationMinutes * 60,
                notes,
                startedAt: new Date(date)
            }
        })
        revalidatePath("/time")
        return { success: true }
    } catch (error) {
        return { error: "Failed to update" }
    }
}

export async function deleteTimeEntry(id: string) {
    const session = await auth()
    if (!session?.user?.id) return { error: "Unauthorized" }

    try {
        await prisma.timeEntry.delete({ where: { id } })
        revalidatePath("/time")
        return { success: true }
    } catch (error) {
        return { error: "Failed to delete" }
    }
}

export async function getDailyEntries(date: Date) {
    const session = await auth()
    if (!session?.user?.id) return []

    const start = new Date(date)
    start.setHours(0, 0, 0, 0)
    const end = new Date(date)
    end.setHours(23, 59, 59, 999)

    return await prisma.timeEntry.findMany({
        where: {
            userId: session.user.id,
            startedAt: { gte: start, lte: end }
        },
        include: {
            project: true,
            workItem: true
        },
        orderBy: { startedAt: 'desc' }
    })
}

export async function getWeeklyEntries(date: Date) {
    const session = await auth()
    if (!session?.user?.id) return []

    const inputDate = new Date(date)
    const day = inputDate.getDay()
    const diff = inputDate.getDate() - day + (day === 0 ? -6 : 1) // adjust when day is sunday
    const startOfWeek = new Date(inputDate.setDate(diff))
    startOfWeek.setHours(0, 0, 0, 0)

    const endOfWeek = new Date(startOfWeek)
    endOfWeek.setDate(startOfWeek.getDate() + 6)
    endOfWeek.setHours(23, 59, 59, 999)

    return await prisma.timeEntry.findMany({
        where: {
            userId: session.user.id,
            startedAt: { gte: startOfWeek, lte: endOfWeek }
        },
        include: {
            project: true,
            workItem: true
        },
        orderBy: { startedAt: 'desc' }
    })
}

export async function getRecentTimeEntries() {
    const session = await auth()
    if (!session?.user?.id) return []

    return await prisma.timeEntry.findMany({
        where: { userId: session.user.id },
        orderBy: { startedAt: "desc" },
        take: 20,
        include: { workItem: { select: { title: true, project: { select: { name: true } } } } }
    })
}

export async function getWeeklySummary() {
    const session = await auth()
    if (!session?.user?.id) return { totalSeconds: 0, entries: [] }

    const now = new Date()
    const startOfWeek = new Date(now.setDate(now.getDate() - now.getDay()))
    startOfWeek.setHours(0, 0, 0, 0)

    const entries = await prisma.timeEntry.findMany({
        where: {
            userId: session.user.id,
            startedAt: { gte: startOfWeek }
        }
    })

    const totalSeconds = entries.reduce((acc, curr) => acc + curr.durationSeconds, 0)
    return { totalSeconds, count: entries.length }
}
export async function getReportData(
    from: Date,
    to: Date,
    filters?: { userId?: string; projectId?: string }
) {
    const session = await auth()
    if (!session?.user?.id) return { error: "Unauthorized" }

    // Basic RBAC for reports - assume Manager/Admin/Senior can see, Associate maybe only own?
    // For now, let's allow access but filter data based on role inside here if strictly needed.
    // The previous implementation of getWorkItems had rigorous RBAC.
    // For simplicity in this overhaul, we'll assume the caller (page.tsx) handles the "who can view the page" check, 
    // and here we just fetch data.

    const whereClause: any = {
        startedAt: {
            gte: from,
            lte: to
        }
    }

    if (filters?.userId) whereClause.userId = filters.userId
    if (filters?.projectId) whereClause.projectId = filters.projectId

    try {
        const entries = await prisma.timeEntry.findMany({
            where: whereClause,
            include: {
                user: { select: { name: true, billableRate: true } },
                project: { select: { name: true, billableRate: true, isBillable: true } },
                workItem: { select: { title: true } }
            },
            orderBy: { startedAt: "desc" }
        })

        // Aggregation
        let totalHours = 0
        let billableHours = 0
        let billableAmount = 0
        let unbilledAmount = 0

        const chartData: Record<string, number> = {} // "YYYY-MM-DD" -> hours

        entries.forEach(e => {
            const hours = e.durationSeconds / 3600
            totalHours += hours

            // Chart data
            const dayKey = e.startedAt.toISOString().split("T")[0]
            chartData[dayKey] = (chartData[dayKey] || 0) + hours

            // Billable logic: 
            // 1. Entry isBillable (overrides project?) -> schema says TimeEntry.isBillable defaults to true
            // 2. Project.isBillable must be true

            // Effective Rate: Use Project rate if set, otherwise User rate
            const effectiveRate = e.project?.billableRate ?? e.user.billableRate ?? 0

            if (e.isBillable && (e.project?.isBillable ?? true)) {
                billableHours += hours
                billableAmount += hours * effectiveRate
            } else {
                unbilledAmount += hours * effectiveRate // Potential lost revenue
            }
        })

        return {
            summary: {
                totalHours,
                billableHours,
                billableAmount,
                unbilledAmount,
                utilization: totalHours > 0 ? (billableHours / totalHours) * 100 : 0
            },
            chartData: Object.entries(chartData).map(([date, hours]) => ({ date, hours })).sort((a, b) => a.date.localeCompare(b.date)),
            entries // Return full list for table
        }

    } catch (error) {
        console.error("Failed to fetch report data:", error)
        return { error: "Failed to fetch report data" }
    }
}
